    align-items: center;
    gap: 10px;
  }
}
@media (min-width: 1px) and (max-width: 320px) {
  .custom_ul__li {
    gap: 18px;
  }
}
.custom_ul__icon {
  display: inline-block;
  width: 32px;
  height: 32px;
  flex-shrink: 0;
}
@media (min-width: 1px) and (max-width: 320px) {
  .custom_ul__icon {
    width: 24px;
    height: 24px;
  }
}
.custom_ul__icon--yellow {
  fill: #f8f200;
}
@keyframes zoom_in_out {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.08);
  }
  100% {
    transform: scale(1);
  }
}
.smooth_appearance_left_main {
  transition: all ease-in-out 0.5s;
}
.smooth_appearance_left_out {
  opacity: 0%;
  transform: translateX(-100px);
}
.smooth_appearance_right_main {
  transition: all ease-in-out 0.5s;
}
.smooth_appearance_right_out {
  opacity: 0%;
  transform: translateX(100px);
}
.smooth_appearance_bottom_main {
  transition: all ease-in-out 0.5s;
}
.smooth_appearance_bottom_out {
  opacity: 0%;
  transform: translateY(100px);
}
.js-animation_slides {
  position: relative;
  overflow: hidden;
}
.js-animation_slides__slide {
  position: absolute;
  opacity: 1;
  transition: opacity linear 0.5s;
  pointer-events: none;
  object-fit: cover;
}
@media (min-width: calc(1024px + 1px)) {
  .js-animation_slides__slide {
    width: min(100%, 1920px);
    height: clamp(713px, 100vh, 1000px);
    animation-name: zoom_in_out;
    animation-duration: 20s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
}
@media (min-width: calc(320px + 1px)) and (max-width: 1024px) {
  .js-animation_slides__slide {
    width: 100%;
    height: clamp(713px, 100vh, 1000px);
    animation-name: zoom_in_out;
    animation-duration: 20s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
}
@media (min-width: 1px) and (max-width: 320px) {
  .js-animation_slides__slide {
    width: max(100%, 320px);
  }
}
